$primary: #222222 !default; $secondary: #ffffff !default; $tertiary: #0088cc !default; $quaternary: #e45735 !default; $header_background: #ffffff !default; $header_primary: #333333 !default; $highlight: #ffff4d !default; $selected: #e9e9e9 !default; $hover: #f2f2f2 !default; $danger: #c80001 !default; $success: #009900 !default; $love: #fa6c8d !default;  $public_image_path: "http://127.0.0.1:4200/images";  @import "common/foundation/variables"; @import "common/foundation/mixins"; .poll-ui-builder.modal {
  .modal-inner-container {
    width: 40em; // scale with user font-size
    max-width: 100vw; // prevent overflow if user font-size is enormous
  }

  .modal-body {
    max-height: unset;
  }

  .poll-number {
    margin-right: 10px;

    &:last-of-type {
      margin-right: 0;
    }
  }

  .options {
    display: flex;
    justify-content: space-between;
  }
  .column {
    display: inline-block;
    width: calc(50% - 10px);
  }

  .d-editor-preview {
    margin-top: 5px;
    padding-top: 5px;
    border-top: 1px solid var(--primary-low);
  }
}
